<template>
  <div class="sys-main">
    <div class="contentbox">
      <div class="panel">
        <div class="panel-heading">
          <div class="panel-title">抽检公告分析
          </div>
        </div>
        <div class="panel-body">
          <div class="casesearch">
            <ul>
              <li>
                <span class="spantitle">生产企业所属区域：</span>
                <el-select v-model="searchObj.area" style="width: 100px;">
                  <el-option v-for="(i, index) in areaList" :key="index" :label="i.name" :value="i.name">
                  </el-option>
                </el-select>
              </li>
              <li>
                <span class="spantitle" style="width:70px;">抽检结果：</span>
                <el-select v-model="searchObj.spotCheck" style="width: 100px;">
                  <el-option v-for="(i, index) in spotCheck" :key="index" :label="i.name" :value="i.name">
                  </el-option>
                </el-select>
              </li>
              <li>
                <el-input v-model="searchObj.keyword" placeholder="请输入关键词" @keydown.enter="init(1)"></el-input>
              </li>
              <li>
                <el-button type="primary" icon="el-icon-search" @click="init(1)">查询</el-button>
              </li>
            </ul>
          </div>

          <el-row>
            <el-table :data='item.content' stripe ref='table' style='width: 100%' class="table table-striped" v-loading="loading">
              <el-table-column width="60" show-overflow-tooltip label='序号' fixed>
                <template slot-scope="scope">
                  {{scope.$index + 1 + (nowPage - 1) * pageSize}}
                </template>
              </el-table-column>
              <el-table-column property='pRD_NAME' width="250" show-overflow-tooltip label='产品名称'>
                <template slot-scope="scope">
                  <span v-html="scope.row.pRD_NAME"></span>
                </template>
              </el-table-column>
              <el-table-column prop='pRD_BRAND' width="100" show-overflow-tooltip label='产品商标'>
                <template slot-scope="scope">
                  <span v-html="scope.row.pRD_BRAND"></span>
                </template>
              </el-table-column>
              <el-table-column prop='pRD_MODEL' width="180" show-overflow-tooltip label='产品型号'>
                <template slot-scope="scope">
                  <span v-html="scope.row.pRD_MODEL"></span>
                </template>
              </el-table-column>
              <el-table-column prop='pRD_BATCH' width="150" show-overflow-tooltip label='产品批号'>
                <template slot-scope="scope">
                  <span v-html="scope.row.pRD_BATCH"></span>
                </template>
              </el-table-column>
              <el-table-column prop='pRD_FROM' width="100" show-overflow-tooltip label='产品产地'>
                <template slot-scope="scope">
                  <span v-html="scope.row.pRD_FROM"></span>
                </template>
              </el-table-column>
              <el-table-column prop='pRD_ENT' width="250" show-overflow-tooltip label='生产企业'>
                <template slot-scope="scope">
                  <span v-html="scope.row.pRD_ENT"></span>
                </template>
              </el-table-column>
              <el-table-column prop='pUR_CHANNEL' width="100" show-overflow-tooltip label='抽检渠道'>
                <template slot-scope="scope">
                  <span v-html="scope.row.pUR_CHANNEL"></span>
                </template>
              </el-table-column>
              <el-table-column prop='rESULT' width="100" show-overflow-tooltip label='抽检结果'>
                <template slot-scope="scope">
                  <span v-html="scope.row.rESULT"></span>
                </template>
              </el-table-column>
              <el-table-column prop='rEMARK' width="100" show-overflow-tooltip label='备注'>
                <template slot-scope="scope">
                  <span v-html="scope.row.rEMARK"></span>
                </template>
              </el-table-column>
              <el-table-column width="80" show-overflow-tooltip label='数据来源'>
                <template slot-scope="scope">
                  <a :href="scope.row.dATA_FROM_URL" target="_blank"><i class="icon-ie"></i></a>
                </template>
              </el-table-column>
              <el-table-column prop='pUBLISH_TIME' width="150" show-overflow-tooltip label='发布时间' fixed="right">
              </el-table-column>
            </el-table>
          </el-row>
          <div class="pagebar" v-show="item.totalRecord>pageSize">
            <div class="pageinfo">共有{{item.totalRecord}}条记录，每页显示{{pageSize}}条</div>
            <el-pagination background layout="prev, pager, next" :total="item.totalRecord" :page-size="pageSize"
              @current-change="init" :current-page.sync="nowPage">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loading: false,
      nowPage: 1,
      pageSize: 10,
      item: {
        content: []
      },
      searchObj: {
        area: '全国',
        spotCheck: '全部',
        keyword: this.$route.query.keyword ? this.$route.query.keyword : ''
      },
      areaList: [],
      spotCheck: [{
        name: '全部'
      },
      {
        name: '合格'
      },
      {
        name: '不合格'
      }
      ]
    }
  },
  created () {
    this.getArea()
  },
  methods: {
    init (p) {
      var self = this
      this.item = {
        content: []
      }
      this.nowPage = p
      let params = this.getParams()
      this.loading = true
      this.$http.get({
        api: 'A_S_NOTICE_ITEM',
        params: params,
        loading: this.loading,
        callback: (res) => {
          self.loading = false
          res.content = self.$lib.highLight(res.content, self.searchObj.keyword)
          self.item = res
        }
      })
    },
    getParams () {
      let obj = {
        pageSize: this.pageSize,
        page: this.nowPage,
        area: this.searchObj.area,
        keyword: this.searchObj.keyword,
        spotCheck: this.searchObj.spotCheck
      }
      return obj
    },
    getArea () {
      let self = this
      this.$http.get({
        api: 'A_S_NOTICE_AREA',
        params: {},
        callback: (res) => {
          res.unshift({
            name: '全国'
          })
          self.areaList = res
          self.init(1)
        }
      })
    }
  }
}

</script>

<style scoped>
  .el-pagination {
    margin-top: 20px;
  }

  .el-pagination .number {
    position: initial;
    width: initial;
  }

  .casesearch li span.el-checkbox__inner,
  .casesearch li span.el-checkbox__input {
    width: 14px;
  }

  .panel-sub-title {
    float: right;
    font-size: 12px;
    font-weight: 300;
    color: #058fcd;
  }

  .casesearch li span.spantitle {
    width: 150px;
  }

  .el-table--fit {
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
  }
</style>



